<script>
  // import { stat } from "fs";

  import _ from "lodash-es";
  import * as d3 from "d3";
  export let name;
  export let ns = "60";
  let color = "#cc";

  // let n = 60;
  // $: (n = +ns), buildDots();
  let state = {
    isStopped: false,
    direction: 1,
    stepsize: 1,
    current_hour: 1,
    distance: 0,
    current_minute: 0,
    target_hour: 11,
    target_minute: 0
  };
  function scale(x0, x) {
    let p0 = x / x0;
    let p1 = (x0 - x) / x;
    let p = Math.min(p0, p1);
    p = p * 1.5;
    return p;
  }

  const tnames = {
    0: "hel",
    5: "fem ver",
    10: "tio ver",
    15: "kvart ver",
    20: "tjugo ver",
    25: "fem i halv",
    30: "halv",
    35: "fem ver halv",
    40: "tjugo i",
    45: "kvart i",
    50: "tio i",
    55: "fem i",
    60: "hel"
  };
  const thours = {
    
    0: "none",
    5: "current",
    10: "current",
    15: "current",
    20: "current",
    25: "next",
    30: "next",
    35: "next",
    40: "next",
    45: "next",
    50: "next",
    55: "next",
    60: "next_none",
  };
  const hnames = {
    0: "tolv",
    1: "ett",
    2: "tv",
    3: "tre",
    4: "fyra",
    5: "fem",
    6: "sex",
    7: "sju",
    8: "tta",
    9: "nio",
    10: "tio",
    11: "elva",
    12: "tolv",
    13: "ett"
  };
  function parseTimeText() {
    let m = state.current_minute;
    m = Math.round(m / 5) * 5;

    let tname = tnames[m];
    let thour = thours[m];
    let hname = hnames[state.current_hour];

    if (thour == "none") {
      return hname;
    }
    if (thour == "next_none") {
      return  hnames[state.current_hour + 1];;
    }
    if (thour == "next") {
      hname = hnames[state.current_hour + 1];
    }
    let s = tname + " " + hname;

    return s;
  }

  let timetext = parseTimeText();

  $: timetext = parseTimeText(state);

  function tick() {
    if (state.isStopped) {
      return;
    }
    let t0 = state.current_hour + state.current_minute / 60;
    let t1 = state.target_hour + state.target_minute / 60;

    let diff = t1 - t0;

    if (diff < 0) {
      diff = 12 + diff;
    }

    if (diff < 1 / 40) {
      state.isStopped = true;
      state.current_hour = state.target_hour;
      state.current_minute = state.target_minute;
    } else {
      let fscale = scale(state.distance, diff) || 1;
      let d = state.stepsize * fscale;
      d = Math.max(d, 0.1 / 60);
      d = Math.min(d, 7 / 60);
      let t2 = t0 + d * state.direction;
      let h = Math.floor(t2 % 12);
      let m = t2 % 1;
      m = m * 60;
      

      if (h < 0) {
        h = 12 + h;
      }
      if (m >= 60){
        m = 0;
        h += 1;
      }
      if (h >= 13){
        h = 12 - h;
      }
      
      state.current_hour = h % 12;
      state.current_minute = m ;
    }

    maphands.minute.val = state.current_minute;
    maphands.hour.val = state.current_hour;
    updateHands();
  }
  function step() {
    tick();
    window.requestAnimationFrame(step);
  }
  window.requestAnimationFrame(step);

  // setInterval(tick, 30);
  let elements = {
    minute: {},
    hour: {}
  };
  let maphands = {
    minute: {
      key: "minute",
      ang: 0,
      val: 15,
      x0: 0,
      y0: 0,
      x1: 0,
      y1: 0,
      lw: 4,
      r0: 0,
      r1: 103,
      rc: 94,
      radius: 5.5,
      vmax: 60,
      adj: 0,
      scolor: "#a6bddb",

      color: "#a6bddb"
    },
    hour: {
      key: "hour",
      radius: 8,
      ang: 0,
      adj: 0,
      val: 3,
      x0: 0,
      y0: 0,
      x1: 0,
      y1: 0,
      lw: 5,
      r0: 0,
      r1: 60,
      rc: 45,

      vmax: 12,
      scolor: "#2b8cbe",
      color: "#2b8cbe"
    }
  };
  let hands = [maphands.minute, maphands.hour];
  let ds = [];
  const pi = 3.141;
  function updateHands() {
    maphands.minute.ang =
      (maphands.minute.val / maphands.minute.vmax) * 2 * pi - pi / 2;
    maphands.hour.adj = maphands.minute.val / 60;

    _.map(hands, d => {
      d.ang = (d.val / d.vmax) * 2 * pi - pi / 2;
      d.ang += (d.adj * pi * 2) / d.vmax;
      d.x0 = Math.cos(d.ang) * d.r0;
      d.x1 = Math.cos(d.ang) * d.r1;
      d.y0 = Math.sin(d.ang) * d.r0;
      d.y1 = Math.sin(d.ang) * d.r1;
      d.xc = Math.cos(d.ang) * d.rc;
      d.yc = Math.sin(d.ang) * d.rc;
    });
    hands = hands;
  }

  function buildDots() {
    let lap = 2 * 3.141;
    let items = [];
    let fi = lap / 4;
    let ang = lap / 60;

    for (let i = 1; i <= 60; i++) {
      let v = i * ang - fi;
      let item = { a: v, n: i, r: 85 };
      item.x = item.r * 1.0 * Math.cos(item.a);
      item.y = item.r * 1.0 * Math.sin(item.a);
      item.x1 = item.r * 0.9 * Math.cos(item.a);
      item.y1 = item.r * 0.9 * Math.sin(item.a);
      item.xx = item.r * 1.1 * Math.cos(item.a);
      item.yy = item.r * 1.1 * Math.sin(item.a);
      item.fz = 3;
      if (i % 5 == 0) {
        item.fz = 7;
        item.xx = item.r * 1.1 * Math.cos(item.a);
        item.yy = item.r * 1.1 * Math.sin(item.a);
      }
      item.color = "#4d4d4d";
      item.lw = 0.5;
      item.t = `${i}`;
      items.push(item);
    }

    for (let i = 1; i <= 60; i++) {
      let v = i * ang - fi;
      let item = { a: v, n: i, r: 85 };
      item.x = item.r * 0 * Math.cos(item.a);
      item.y = item.r * 0 * Math.sin(item.a);
      item.x1 = item.r * 0 * Math.cos(item.a);
      item.y1 = item.r * 0 * Math.sin(item.a);
      item.fz = 0;

      if (i % 5 == 0) {
        item.fz = 5;

        item.xx = item.r * 1.25 * Math.cos(item.a);
        item.yy = item.r * 1.25 * Math.sin(item.a);
      }
      item.color = "#7b7b7b";
      item.lw = 0.2;

      item.t = `-${60 - i}`;
      if (i < 30) {
        continue;
        item.t = "";
      }
      if (i == 60) {
        item.t = "";
      }
      items.push(item);
    }

    ang = lap / 12;

    _.map(_.range(12), i => {
      let v = (i + 0.5) * ang - fi;
      let item = { a: v, n: i, r: 85 };
      item.x = item.r * 1.15 * Math.cos(item.a);
      item.y = item.r * 1.15 * Math.sin(item.a);
      item.x1 = item.r * 0.85 * Math.cos(item.a);
      item.y1 = item.r * 0.85 * Math.sin(item.a);
      item.fz = 0;
      item.fz = 0;

      item.xx = item.r * 1.25 * Math.cos(item.a);
      item.yy = item.r * 1.25 * Math.sin(item.a);

      item.color = "#7b7b7b";
      item.lw = 0.2;
      item.t = "";
      items.push(item);
    });

    ang = lap / 12;

    for (let i = 1; i <= 12; i++) {
      let v = i * ang - fi;
      let item = { a: v, n: i, r: 85 };
      item.x = item.r * 0.8 * Math.cos(item.a);
      item.y = item.r * 0.8 * Math.sin(item.a);
      item.x1 = item.r * 1 * Math.cos(item.a);
      item.y1 = item.r * 1 * Math.sin(item.a);
      item.xx = item.r * 0.7 * Math.cos(item.a);
      item.yy = item.r * 0.7 * Math.sin(item.a);
      item.color = "#4d4d4d";

      item.lw = 2;
      item.fz = 12;

      item.t = `${i}`;
      items.push(item);
    }
    ang = lap / 4;
    for (let i = 1; i <= 4; i++) {
      let v = i * ang - fi;
      let item = { a: v, n: i, r: 85 };
      item.x = item.r * 0.9 * Math.cos(item.a);
      item.y = item.r * 0.9 * Math.sin(item.a);
      item.x1 = item.r * 1.0 * Math.cos(item.a);
      item.y1 = item.r * 1.0 * Math.sin(item.a);
      item.xx = item.r * 0.2 * Math.cos(item.a);
      item.yy = item.r * 0.2 * Math.sin(item.a);
      item.color = "#4d4d4d";
      item.t = "";

      item.lw = 4;
      item.fz = 5;
      items.push(item);
    }
    ang = lap / 4;
    for (let i = 1; i <= 4; i++) {
      let v = i * ang - fi;
      let item = { a: v, n: i, r: 85 };
      item.x = item.r * 0.78 * Math.cos(item.a);
      item.y = item.r * 0.78 * Math.sin(item.a);
      item.x1 = item.r * 1.0 * Math.cos(item.a);
      item.y1 = item.r * 1.0 * Math.sin(item.a);
      item.xx = item.r * 1.21 * Math.cos(item.a);
      item.yy = item.r * 1.21 * Math.sin(item.a);
      item.color = "#4d4d4d";
      item.t = "";
      item.lw = 0.3;
      item.fz = 5;
      items.push(item);
    }

    ds = items;
  }
  function mouseHandler(e) {
    let el = e.currentTarget;
    const rect = e.currentTarget.getBoundingClientRect();
    var m = el.getScreenCTM();
    var p = el.createSVGPoint();

    p.x = e.clientX;
    p.y = e.clientY;
    p = p.matrixTransform(m.inverse());
    cursor.x2 = p.x;
    cursor.y2 = p.y;
  }
  function mouseClickHandler(e) {
    // console.log("el clicko");

    let el = e.currentTarget;

    var m = el.getScreenCTM();
    var p = el.createSVGPoint();

    p.x = e.clientX;
    p.y = e.clientY;
    p = p.matrixTransform(m.inverse());

    let r = Math.sqrt(p.x * p.x + p.y * p.y);

    let ang = Math.atan2(p.y, p.x) + pi / 2;
    if (ang < 0) {
      ang = 2 * pi + ang;
    }
    // f = f - pi/4;
    // let ang = f/2/pi*360;

    cursor.x2 = p.x;
    cursor.y2 = p.y;
    let hand = maphands.hour;
    let ss = "target_hour";
    if (r > 85) {
      ss = "target_minute";
      hand = maphands.minute;
      let v = (ang / 2 / pi) * hand.vmax;
      v = Math.round(v);
      state.target_minute = v;
    } else {
      ss = "target_hour";
      hand = maphands.hour;
      let v = (ang / 2 / pi) * hand.vmax;

      let v0 = v % 1;
      v0 = v0 * 60;
      let v1 = Math.floor(v % 12);

      state.target_minute = v0;
      state.target_hour = v1;
    }
    state.stepsize = 0.5;
    state.direction = 1;

    let dh = state.target_hour - state.current_hour;
    let dm = state.target_minute - state.current_minute;
    let dd = dh + dm / 60;

    state.distance = dd;
    if (dd < 0) {
      state.distance = 12 + dd;
    }
    state.isStopped = false;
  }

  $: ds;
  $: hands;
  buildDots();
  updateHands();
  let cursor = {
    x1: 0,
    y1: 0,
    x2: 10,
    y2: 10
  };
  $: state;
</script>

<style>
  @page {
    margin: 0.5cm;
  }

  h1 {
    color: #ff3e00;
    text-transform: uppercase;
    font-size: 4em;
    font-weight: 100;
  }
  .digits {
    font-family: Courier, Monaco, monospace;

    display: inline-block;
    font-size: 32px;
  }
</style>

<main style="max-width:720px;">
  <div style="width:100%">

    <svg
      viewBox="-125 -125 250 250"
      on:mousemove={mouseHandler}
      on:mousedown={mouseClickHandler}>
      <g>
        {#each hands as d}
          <circle
            cx={d.xc}
            cy={d.yc}
            r={d.radius}
            stroke={d.scolor}
            fill={d.scolor}
            stroke-width={0} />

          <line
            x1={d.x0}
            y1={d.y0}
            stroke-linecap="round"
            y2={d.y1}
            x2={d.x1}
            stroke={d.scolor}
            stroke-width={d.lw} />
        {/each}
      </g>
      <g>
        <circle
          cx="0"
          cy="0"
          r="85"
          style="user-select: none;"
          stroke="#4d4d4d"
          fill="none"
          stroke-width="2" />
        <g>

          {#each ds as d}
            <line
              x1={d.x1}
              y1={d.y1}
              style="user-select: none;"
              y2={d.y}
              x2={d.x}
              stroke={d.color}
              stroke-width={d.lw} />
          {/each}
        </g>
        <g>
          {#each ds as d}
            <text
              x={d.xx}
              y={d.yy + d.fz / 3}
              font-size={d.fz}
              style="user-select: none;"
              text-anchor="middle"
              fill={d.color}>
              {d.t}
            </text>
          {/each}
        </g>

      </g>
      <line
        x1={cursor.x1}
        y2={cursor.y2}
        x2={cursor.x2}
        y1={cursor.x1}
        stroke="#ef8a62"
        stroke-width="0.5" />
      <circle
        cx={cursor.x2}
        cy={cursor.y2}
        r="1"
        stroke="#ef8a62"
        fill="#ef8a62"
        stroke-width="0.5" />

      <g>

        <text
          x="0"
          y="-17"
          font-size="10"
          text-anchor="middle"
          fill="#ffffff"
          stroke="#202020"
          stroke-width="0.5">
          {timetext}
        </text>
        <text x="0" y="-17" font-size="10" text-anchor="middle" fill="#e34a33">
          {timetext}
        </text>
      </g>
      <g>
<!-- 
        <text
          x="0"
          y="18"
          font-size="8"
          text-anchor="middle"
          fill="#ffffff"
          stroke="#4d4d4d"
          stroke-width="0.5">
          {d3.format('02.0f')(state.current_hour)}:{d3.format('02.0f')(state.current_minute)}
        </text> -->
        <text x="0" y="18" font-size="8" text-anchor="middle" fill="#fdbb84">
          {d3.format('02.0f')(state.current_hour)}:{d3.format('02.0f')(state.current_minute)}
        </text>


        <text x="0" y="28" font-size="8" text-anchor="middle" fill="#fdbb84" style="opacity:0.5">
          {d3.format('02.0f')(state.current_hour+12)}:{d3.format('02.0f')(state.current_minute)}
        </text>
      </g>

    </svg>

  </div>
</main>
